@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);

a{
  text-decoration: none;
  color: black;
}

.wrapper{
 background-color: lavenderblush;
  padding:10px;
display: flex;
justify-content:space-around;
align-items: center;
margin: 50px 0px;
flex-wrap: wrap;
}
.box{
  width:300px;
overflow: hidden;
  border-radius: 10px;
  padding:20px 10px;
  background-color: white;

}

.image{
  display: flex;
  justify-content: center;
} 
.image img{
  width:200px;
  height:250px;

  
}
.addToCartBtn{
  border-radius:10px;
  padding: 10px 50px;
  transition: all 0.5s ease;
}
.desc{
  margin-top:10px;
  line-height: 10px;
}

.addToCartBtn:hover{
  background-color: black;
  color:white;
}
.whatsAppbtn{
  font-size: 30px;
  cursor: pointer;
}
.boxFooter{
  display: flex;
  justify-content:space-around;
  align-items: center;
}
.productDetails{
  margin: 50px 100px;
  border-radius: 10px;
  overflow: hidden;
  display:flex;
  box-shadow: 0px 2px 13px -1px rgba(0,0,0,0.75);
  justify-content:space-between;
  padding: 10px;
}
.singleImage img{
  width:300px;
  height:350px
}
.productDetails .desc{
  margin-left: 30px;
}
.btn button{
  border:none;
  padding:10px 20px;
  background:black;
  border-radius: 10px;
  color:white
}
.cartWrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top:30px;
}
.cartWrapper .model{
  cursor: pointer;
}

.cartItemImage{
  position: relative;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding:10px;
}

.cartItemImage img{
  width:100px;
  height:100px;
  
}
.cartItemWrapper{
  width:500px;
  
  margin: 20px 0px;

  display:flex;

  align-items: center;
  padding: 10px;
  border-radius: 10px;
  overflow: hidden;
  grid-gap:15px;
  gap:15px
}
.productPrice:nth-child(3){
  margin-left: auto;
}
.qty input{
  border-radius: 8px;
  width: 50px;
  margin-left: 5px;
  border:1px ;
}
.delete span{

  color:black;
  position:absolute;
  top:0;
  right:0;
cursor:pointer

}
.cartPrice .price{
  font-size:20px;
  font-weight: bold;
}
.checkoutBtn button{
  padding:5px 10px;
  font-size: 12px;
border:none;
background-color: black;
color:white;
border-radius: 10px;
}
.checkoutBtn span{
  margin-left: 20px;
  color:green;
  cursor:pointer;
}
.cartSummary{
margin-top: 20px;
}

.model{
  position:relative;
}
.model .overlay{
  width:78%;
  height:100vh;
  background-color:rgba(46, 43, 43, 0.8);
}
.model .modelContent{
  /* transition: all .5s ease ; */
  padding:20px;
  height:100vh;
  background-color:white;
  position: absolute;
  top:0;
  right:0;
}

.model .modelHeader{
  display: flex;
  justify-content: space-around;
}

.model .offers{
  border:1px solid lightgrey;
  margin:20px 0px;
  padding:10px;
  border-radius: 10px;
}

@media only screen and (max-width:600px){
  .productDetails{
    width: 100%;
    margin: 30px 0px;
  }
  .box{
    margin-top: 20px;
  }
  .cartItemWrapper{
    width:100%;
  }
}

